<template>
  <div class="main">
      <!-- 二级组件components下的main @change="toggle" -->
      <div class="homebg">
          <router-view></router-view>
      </div>
      <van-tabbar v-model="active" active-color="rgb(249,187,50)" :route="true">
        
      <!-- <van-tabbar-item  v-for="(item,index) in tabBar" :key="index" :icon="item.icon">{{item.title}}</van-tabbar-item> -->
      <van-tabbar-item  v-for="(item,index) in tabBar" :key="index" :icon="item.icon" :to="item.url">{{item.title}}</van-tabbar-item>
      
    </van-tabbar>
  </div>
</template>

<script>
export default {
  data() {
    return {
        isShow:true,
        active:0,
        tabBar: [
          {
            icon: 'wap-home-o',
            title: '首页',
            routeName:'Home',
            url:'/main/home'
              
          },
          {
            icon: 'coupon-o',
            title: '菜单',
            routeName:'Menu',
            url:'/main/menu'
              
          },
          // {
          //   icon: 'orders-o',
          //   title: '订单',
          //   routeName: 'Order',
          //   url:'/main/order'
          // },
          {
            icon: 'shopping-cart-o',
            title: '购物车',
            routeName: 'Cart',
            url:'/main/cart'
          },
          {
            icon: 'contact',
            title: '我的',
            routeName: 'My',
            url:'/main/my'
          },
    ]
    };
  },
  methods:{
      
      //切换底部导航
      toggle(index){
          
          this.$router.push({name:this.tabBar[index].routeName});
      }
  },
  created(){
    let url = location.hash.slice(1);
    
    
    for(let i =0;i<this.tabBar.length;i++){
      if(this.tabBar[i].url == url){
        
        this.active = i;
        break;
      }
    }
  }
};
</script>

<style lang="less" scoped>

.van-tabbar-item--active {
    color: rgb(249,187,50);
}


</style>